import React from "react"
import { Link } from "gatsby"

import 'bootstrap/dist/css/bootstrap.min.css'
import Navbar from 'react-bootstrap/Navbar'
// import NavDropdown from 'react-bootstrap/NavDropdown'
import Nav from 'react-bootstrap/Nav'

import { FaYoutube, FaMedium, FaInstagram, FaTwitter } from 'react-icons/fa';

import { rhythm, scale } from "../utils/typography"

const Layout = ({ location, title, children }) => {
  const rootPath = `${__PATH_PREFIX__}/`
  let header

  if (location.pathname === rootPath) {
    header = (
      <h1
        style={{
          ...scale(1.5),
          marginBottom: rhythm(1.5),
          marginTop: 0,
        }}
      >
        <Link
          style={{
            boxShadow: `none`,
            color: `inherit`,
          }}
          to={`/`}
        >
          {title}
        </Link>
      </h1>
    )
  } else {
    header = (
      <h3
        style={{
          fontFamily: `Roboto`,
          marginTop: 0,
        }}
      >
        <Link
          style={{
            boxShadow: `none`,
            color: `inherit`,
          }}
          to={`/`}
        >
          {title}
        </Link>
      </h3>
    )
  }
  return (
    <div
      style={{
        marginLeft: `auto`,
        marginRight: `auto`,
        maxWidth: '1170px',
        padding: '10px',
      }}
    >
      {/* Nav */}
      <Navbar collapseOnSelect expand="md" style={{
        marginBottom: '30px',
      }}>
        <Navbar.Brand href="/" style={{
          fontSize: '30px', boxShadow: `none`,
        }}>Benjamin J. Carlson</Navbar.Brand>
        <Navbar.Toggle aria-controls="responsive-navbar-nav" />
        <Navbar.Collapse id="responsive-navbar-nav">
          <Nav className="ml-auto">
            <Nav.Link href="" style={{ color: '#000', boxShadow: `none` }}>Gear</Nav.Link>
            <Nav.Link href="https://www.youtube.com/channel/UCLMdmCCRFGWt7rktx6tMErw" style={{ boxShadow: `none` }} target='_blank'><FaYoutube style={{ fontSize: '30px', color: '#c4302b' }} /></Nav.Link>
            <Nav.Link href="https://medium.com/@benjamincarlson" style={{ boxShadow: `none` }} target='_blank'><FaMedium style={{ fontSize: '30px', color: '#000' }} /></Nav.Link>
            <Nav.Link href="https://www.instagram.com/benscstutorials/" style={{ boxShadow: `none` }} target='_blank'><FaInstagram style={{ fontSize: '30px', color: '#bc2a8d' }} /></Nav.Link>
            <Nav.Link href="https://twitter.com/benscstutorials" style={{ boxShadow: `none` }} target='_blank'><FaTwitter style={{ fontSize: '30px', color: '#1DA1F2' }} /></Nav.Link>
          </Nav>
        </Navbar.Collapse>
      </Navbar>
      <header>{header}</header>
      <main>{children}</main>
      <footer style={{ textAlign: 'center', marginTop: '10px' }}>
        2019 - {new Date().getFullYear()} | Benjamin J. Carlson
      </footer>
    </div>
  )
}

export default Layout
